import { XR_AXES, XR_BUTTONS } from "gamepad-wrapper";

// import { Component, System } from "elics";
import { globals } from "./global";

import ThreeMeshUI from "three-mesh-ui";
import { PDFRequestComponent } from "./pdfviewer";
import { createComponent, createSystem, Types } from "elics";
import { CachedPageRequestComponent, CacheLoadBookRequestComponent } from "./pagescache";

const FontJSON = "assets/Roboto-msdf.json";
const FontImage = "assets/Roboto-msdf.png";

const SETTING_ENTRIES = {
  unit: { position: [0, 0.01, 0.001], options: ["Metric", "Imperial"] },
  mode: { position: [0, -0.1775, 0.001], options: ["Tape", "Clamp"] },
  tips: { position: [0, -0.365, 0.001], options: ["On", "Off"] },
};

const DIRECTIONS = {
  Up: "up",
  Down: "down",
  Left: "left",
  Right: "right",
  None: "none",
};

const bookSchema = {
	book: { type: Types.Object, default: {} },
};

export const BookComponent = createComponent(bookSchema, (_index) => {}, (_index) => {
  if(this.bookPanel) this.bookPanel.removeFromParent();
});

const pagesImgsSchema = {
	cache: { type: Types.Object, default: {} },
};

export const PagesImgsCacheComponent = createComponent(pagesImgsSchema, (_index) => {}, (_index) => {
  // TODO implement empty cache
});

const readerUpdateSchema = {
	payload: { type: Types.Object, default: {} },
};

export const ReaderUpdatePageComponent = createComponent(readerUpdateSchema);

const readerUpdateBookSchema = {
	payload: { type: Types.Object, default: {} },
};

export const ReaderUpdateBookComponent = createComponent(readerUpdateBookSchema);

const queries = {
	updateRequests: { required: [ReaderUpdatePageComponent] },
	updateBookRequests: { required: [ReaderUpdateBookComponent] },
};

export class ReaderSystem extends createSystem(queries) {
  init() {
    
    // TODO these vectors are related to a creating a panel 
    // that moves following the user's head position.
    // It could be useful for creating a help or config panel
    this._vec3 = new Vector3();
    this._targetVec3 = new Vector3();

    this.currentBookSize = 0.75;

    this.bookEntity = this.world.createEntity();

    this.bookEntity.addComponent(BookComponent, 
      {
        book: {
          bookPanel: null,
          pageTexture: null,
          pageNum: 0,
        }
      });

    this.queries.updateRequests.subscribe('qualify', (entity) => {
      //- console.log(`ReaderSystem: updateRequests entity ${entity.id} qualified`);
    });

    this.queries.updateBookRequests.subscribe('qualify', (entity) => {
      //- console.log(`ReaderSystem: updateRequests entity ${entity.id} qualified`);
    });

    // Bind to webpage elements and events
    

    this.bindToWebpageElements();

    // TODO kept here as reference for the future, 
    // to create config params
    Object.entries(SETTING_ENTRIES).forEach(([key, config]) => {
      globals.valueStore.set(key, config.options[0]);
    });
  }

  bindToWebpageElements() {
    this.pdfUrlInput = document.getElementById("pdf-url");
    this.loadPdfButton = document.getElementById("load-pdf");
    
    this.loadPdfButton.addEventListener("click", () => { 
      this.requestBook(this.pdfUrlInput.value, "url");
    });
    this.mrButton = document.getElementById('mr-button');

    this.fileInput = document.getElementById("browse-pdf");
    this.fileInput.addEventListener("change", this.handleFileSelect.bind(this));

  }

  requestBook(pdfUrl, source = "url") {
    const payload = {
      type: "loadPdf",
      source,
      pdfUrl,
    }
    
    this.world.createEntity().addComponent(CacheLoadBookRequestComponent, { payload });
  }

  requestPage(pageNum) {
    // Send a request to the PDF viewer to change the page
    // via adding a request component to the world
    const type = "loadPage";
    const payload = { type, pageNum };

    this.world.createEntity().addComponent(CachedPageRequestComponent, { payload });
  }

  update(_delta) {
    const updateRequests = this.queries.updateRequests.entities;
    const updateBookRequests = this.queries.updateBookRequests.entities;

    let book = this.bookEntity.getValue(BookComponent, 'book');

    updateBookRequests.forEach(e => {
      const payload = e.getValue(ReaderUpdateBookComponent, 'payload');

      this.mrButton.disabled = false;
      this.mrButton.style.display = 'block';
        
      globals.status("\n1. Click on the (big) 'Enter XR' button \n2. Only the *left* controller is enabled \n3. Use the left controller's *thumbstick* to navigate & zoom the book \n4. Use the *Meta 'infinite' button* to exit XR");

      // TODO here for debugging, it will enter XR mode automatically just after loading the book
      this.mrButton.click();
        
      // TODO if pageBitmap is null, it should use a default image
      if(book.bookPanel) {
        book.bookPanel.removeFromParent();
        book.bookPanel = null;
      }

      book.pageTexture = null;
      book.oddPageNum = 0;
      book.evenPageNum = 0;
      book.bookInfo = payload.bookInfo;

      // Update status of the book
      this.bookEntity.setValue(BookComponent, 'book', book);

      e.removeComponent(ReaderUpdateBookComponent)
        .destroy();
    });
  
	  const controller = globals.controllers['left'];

    if (!controller) return;

    if (!book.bookPanel) {
      book.bookPanel = this.makeBookPanel();
      book.bookPanel.position.set(0, 0.5, -0.5);
      controller.raySpace.add(book.bookPanel);
   
      this.requestPage(1);

      if(book.bookInfo.numPages > 1) {
        this.requestPage(2);
      }
    }
   
    // Process update requests from the cache system
    updateRequests.forEach(e => {
      const payload = e.getValue(ReaderUpdatePageComponent, 'payload');
      // TODO pageNum is not used, but will be used in the future
      const { pageNum, pageWidth, pageHeight, pageTexture, bookInfo } = payload;

      //- console.log("updateRequest payload", payload);
      
      book.pageTexture = pageTexture;
      book.pageWidth = pageWidth;
      book.pageHeight = pageHeight;
      book.bookInfo = bookInfo;

      if(pageNum % 2 === 0) {
        book.evenPageNum = pageNum;
      } else {
        book.oddPageNum = pageNum;
      }

      book.bookPanel.updatePage(pageNum, pageTexture, pageWidth, pageHeight, bookInfo);

      // FIXME: this is broken!, it doesn't actually remove the request, which causes
      // the system to keep processing the same request over and over again until a new one is created!
      e.removeComponent(ReaderUpdatePageComponent)
        .destroy();

      // Update status of the book
      this.bookEntity.setValue(BookComponent, 'book', book);
    });

    globals.renderer.xr.addEventListener("sessionstart", () => {
      this._syncTransform();
    });

    const leftGamepad = globals.controllers.left?.gamepad;
    const rightGamepad = globals.controllers.right?.gamepad;

    // Required for positioning after starting the XR session
    globals.playerHead.getWorldPosition(this._vec3);

    const gamepad = rightGamepad?.get2DInputValue(XR_BUTTONS.THUMBSTICK)
      ? rightGamepad
      : leftGamepad;

      // TODO refactor this code to determine the direction of the thumbstick
      // to a separate function
    if (gamepad?.get2DInputValue(XR_BUTTONS.THUMBSTICK) > 0.7) {
      const angle = gamepad.get2DInputAngle(XR_BUTTONS.THUMBSTICK);
      let direction = DIRECTIONS.None;
      if (Math.abs(angle) < Math.PI / 6) {
        direction = DIRECTIONS.Up;
      } else if (Math.abs(angle) > (Math.PI / 6) * 5) {
        direction = DIRECTIONS.Down;
      } else if (
        Math.abs(angle) > Math.PI / 3 &&
        Math.abs(angle) < (Math.PI / 3) * 2
      ) {
        if (angle > 0) {
          direction = DIRECTIONS.Right;
        } else {
          direction = DIRECTIONS.Left;
        }
      }
      if (direction !== DIRECTIONS.None && direction !== this._prevDirection) {
        const hapticActuator = gamepad._gamepad.hapticActuators
          ? gamepad._gamepad.hapticActuators[0]
          : null;
        if (direction === DIRECTIONS.Up || direction === DIRECTIONS.Down) {
          const sizeDelta = direction === DIRECTIONS.Up ? 0.1 : -0.1;

          let newSize = this.currentBookSize + sizeDelta;
          newSize = Math.max(0.1, Math.min(newSize, 2.0));

          book.bookPanel.setSize(newSize);

          this.currentBookSize = newSize;

          hapticActuator?.pulse(0.2, 100);
        } else if (
          direction === DIRECTIONS.Left ||
          direction === DIRECTIONS.Right
        ) {

          // Send a request to the PDF viewer to change the page
          // via adding a request component to the world
          // const type = "loadPage";
          const oddPageNum = (direction === DIRECTIONS.Left) 
            ? book.oddPageNum - 2 
            : book.oddPageNum + 2;

          const evenPageNum = oddPageNum + 1;

          const inRange = (pageNum) => {
            return pageNum >= 1 && pageNum <= book.bookInfo.numPages;
          };

          if(!inRange(oddPageNum) && !inRange(evenPageNum)) {
            // we are either at the first page or the last page of the book,
            // so we don't need to do anything
            return;
          }

          [oddPageNum, evenPageNum].forEach((pageNum) => {
            if (inRange(pageNum)) {
              this.requestPage(pageNum);
            } else {
              // TODO maybe consider a default image for an "empty" page?
              // for now, texture=null means "hidden"
              book.bookPanel.updatePage(pageNum, null, 0, 0);
            }
          });

          hapticActuator?.pulse(0.3, 50);
        }
      }
      this._prevDirection = direction;
    } else {
      this._prevDirection = DIRECTIONS.None;
    }

    let x = gamepad?.getAxis(XR_AXES.THUMBSTICK_X) ?? 0;
    let y = gamepad?.getAxis(XR_AXES.THUMBSTICK_Y) ?? 0;
    if (this._outlineCircle) {
      this._outlineCircle.position.fromArray(
        SETTING_ENTRIES[this._currentSetting].position
      );
      this._outlineCircle.position.x += x * 0.01;
      this._outlineCircle.position.y -= y * 0.01;
    }

    // Update status of the book
    this.bookEntity.setValue(BookComponent, 'book', book);
  }

  async handleFileSelect(event) {
    const file = event.target.files[0];

    if(!file) {
      return;
    }

    if (file.type !== 'application/pdf') {
      alert('Please select a PDF file');
      return;
    }

    this.requestBook(file, "local");
  
    // const fileUrl = await this.readFile(file);

    // this.requestBook(fileUrl);
  }

  async readFile(file) {
    // Helper function to promisify FileReader
    const readAsArrayBuffer = (file) => {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = () => reject(new Error('File reading failed'));
        reader.readAsArrayBuffer(file);
      });
    };
  
    try {
      // Read file contents
      const arrayBuffer = await readAsArrayBuffer(file);
      
      // Create Blob from the contents
      const pdfBlob = new Blob([arrayBuffer], { type: 'application/pdf' });
      
      // Create Object URL
      const objectUrl = URL.createObjectURL(pdfBlob);
      
      return objectUrl;
      
    } catch (error) {
      console.error('Error reading file:', error);
      alert(`Error reading file: ${error}`);
      goblals.status(`Error reading file: ${error}`);
      globals.debug(`Error reading file: ${error}`);
      if(error.stack) {
        globals.debug(`Error stack: ${error.stack}`);
      }
      throw error; // Re-throw for caller to handle
    }
  }

  makeBookPanel() {

    const bookPanel = new ThreeMeshUI.Block({
      ref: "container",
      padding: 0.025,
      fontFamily: FontJSON,
      fontTexture: FontImage,
      fontColor: new Color(0xffffff),
      backgroundOpacity: 0,
    });

    const bookTitle = new ThreeMeshUI.Block({
      height: 0.2,
      width: 1.5,
      margin: 0.025,
      justifyContent: "center",
      fontSize: 0.09,
    });

    const bookTitleTxt = new ThreeMeshUI.Text({
      content: "Book Title",
    });
    
    const oddPagePanel = new ThreeMeshUI.Block({
      name: "pageSubBlock",
      height: 1.0,
      width: 1.0,
      borderWidth: 0.01,
      // [topLeftRadius, topRightRadius, bottomRightRadius, bottomLeftRadius]
      borderRadius: [ 0.01, 0.05, 0.05, 0.01 ],
      borderColor: new Color(0xf5f5f5),
      borderOpacity: 1,
      margin: 0.0,
      padding: 0.0,
      textAlign: "left",
      justifyContent: "end",
    });

    const evenPagePanel = new ThreeMeshUI.Block({
      name: "pageSubBlock",
      height: 1.0,
      width: 1.0,
      borderWidth: 0.01,
      // [topLeftRadius, topRightRadius, bottomRightRadius, bottomLeftRadius]
      borderRadius: [ 0.05, 0.01, 0.01, 0.05 ],
      borderColor: new Color(0xf5f5f5),
      borderOpacity: 1,
      margin: 0.0,
      padding: 0.0,
      textAlign: "left",
      justifyContent: "end",
    });

    const oddPageNum = new ThreeMeshUI.Block({
      height: 0.07,
      width: 0.17,
      borderWidth: 0,
      borderRadius: [ 0, 0, 0, 0 ],
      textAlign: "center",
      justifyContent: "center",
    });

    const oddPageNumTxt = new ThreeMeshUI.Text({
      content: "Page 0",
      fontSize: 0.02,
    });

    const evenPageNum = new ThreeMeshUI.Block({
      height: 0.07,
      width: 0.17,
      borderWidth: 0,
      borderRadius: [ 0, 0, 0, 0 ],
      textAlign: "center",
      justifyContent: "center",
    });

    const evenPageNumTxt = new ThreeMeshUI.Text({
      content: "Page 0",
      fontSize: 0.02,
    });

    // TODO book info not used yet, just for debugging purposes
    const bookInfo = new ThreeMeshUI.Block({
      margin: 0.025,
    });

    const bookInfoTitleTxt = new ThreeMeshUI.Text({
      content: "Book Info",
      fontColor: new Color(0x92e66c),
    })

    const bookInfoTitle = new ThreeMeshUI.Block({
      height: 0.35,
      width: 0.5,
      margin: 0.025,
      padding: 0.02,
      fontSize: 0.04,
      justifyContent: "center",
      backgroundOpacity: 0,
    });

    

    const bookInfoTxt = new ThreeMeshUI.Text({
      content: "No book info available.",
    });

    const bookInfoContents = new ThreeMeshUI.Block({
      height: 0.53,
      width: 0.5,
      margin: 0.01,
      padding: 0.02,
      fontSize: 0.025,
      alignItems: "start",
      textAlign: "justify",
      backgroundOpacity: 0,
    });

    const contentContainer = new ThreeMeshUI.Block({
      contentDirection: "row",
      padding: 0.02,
      margin: 0.025,
      backgroundOpacity: 0,
    });
    
    bookPanel
      .add(
        bookTitle.add(bookTitleTxt)
    );

    bookPanel
      .add(
        contentContainer
          .add(
            oddPagePanel
              .add(
                  oddPageNum.add(oddPageNumTxt)), 
            evenPagePanel
              .add(
                  evenPageNum.add(evenPageNumTxt)), 
            // bookInfo
            //   .add(
            //       bookInfoTitle.add(bookInfoTitleTxt), 
            //       bookInfoContents.add(bookInfoTxt)
            //   )
          )
      );

    /*
    bookPanel
      .add(
        bookTitle.add(bookTitleTxt)
    );

    bookPanel
      .add(
        contentContainer
          .add(
            pagePanel
              .add(
                pageNum.add(pageNumTxt)), 
            bookInfo
              .add(
                  bookInfoTitle.add(bookInfoTitleTxt), 
                  bookInfoContents.add(bookInfoTxt)
              )
          )
      );

    */

    // bookTitle.add(bookTitleTxt);
    // bookPanel.add(bookTitle);
    // pageNum.add(pageNumTxt);
    // pagePanel.add(pageNum);
    // bookInfoTitle.add(bookInfoTitleTxt);
    // bookInfoContents.add(bookInfoTxt);
    // bookInfo.add(bookInfoTitle, bookInfoContents);
    // contentContainer.add(pagePanel, bookInfo);
    // bookPanel.add(contentContainer);

    // TODO this is a naive approach to set the size of the book panel,
    // it should be done taking into account the relative sizes, font sizes, etc.
    bookPanel.setSize = (s) => {
      bookPanel.scale.set(s, s, s);
    }

    bookPanel.updatePage = (pNum, pTexture, pWidth, pHeight, bInfo) => {
      const even = pNum % 2 === 0;
      const pagePanel = even ? evenPagePanel : oddPagePanel;
      const pageNum = even ? evenPageNum : oddPageNum;
      const pageNumTxt = even ? evenPageNumTxt : oddPageNumTxt;
      
      // pWidth and pHeight are in CSS pixels: convert to meters, 
      // the unit used in three.js
      const pixelsPerMeter = 1500.0;
      const width = pWidth / pixelsPerMeter;
      const height = pHeight / pixelsPerMeter;
      
      if(pTexture) {
        pagePanel.set({
          width: width,
          height: height,
          backgroundSize: "stretch",
          backgroundTexture: pTexture,
        });

        pageNumTxt.set({
          content: `Page ${pNum}`,
        });

        pagePanel.visible = true;
        pageNum.visible = true;
        pageNumTxt.visible = true;

        // TODO not used yet, just for debugging purposes
        // Iterate over bInfo keys and values and create a text with format: key: value,
        // separated by new lines
        const bookInfoStr = bInfo 
          ? Object.entries(bInfo)
              .map(([key, value]) => `${key}: ${value}`)
              .join("\n")
          : "N/A";

        bookInfoTxt.set({
          content: bookInfoStr,
        });
      } else {
        pagePanel.set({
          width: width,
          height: height,
          backgroundSize: "stretch",
          backgroundTexture: null,
        });

        pageNumTxt.set({
          content: `Page 0`,
        });

        pagePanel.visible = false;
        pageNum.visible = false;
        pageNumTxt.visible = false;
      }
    };
    
    bookPanel.setSize(this.currentBookSize);

    return bookPanel;
  }

  _syncTransform() {
    this._target.getWorldPosition(this._settingsPanel.position);
    globals.playerHead.getWorldPosition(this._vec3);
    this._vec3.y = this._settingsPanel.position.y;
    this._settingsPanel.lookAt(this._vec3);
  }
}


