import { globals } from "./global";
import { Types, createSystem, createComponent } from "elics";
import { ReaderUpdatePageComponent } from "./reader";
import { CacheUpdateBookComponent, CacheUpdatePageComponent } from "./pagescache";
// import * as mupdf from "mupdf";
import * as viewer from "./mupdf/simple-viewer/viewer";

const pdfRequestSchema = {
	payload: { type: Types.Object, default: {} },
  status: { type: Types.String, default: "pending" },
};

export const PDFRequestComponent = createComponent(pdfRequestSchema);

const loadPDFRequestSchema = {
	payload: { type: Types.Object, default: {} },
  status: { type: Types.String, default: "pending" },
};

export const LoadPDFRequestComponent = createComponent(loadPDFRequestSchema);
  
const DESIRED_DPI = 300; // Target print-quality resolution

const queries = {
	pageRequests: { required: [PDFRequestComponent] },
	loadPDFRequests: { required: [LoadPDFRequestComponent] },
};


export class PDFLoaderSystem extends createSystem(queries) {
  init() {
    this.initStatusProperties();

    this.queries.pageRequests.subscribe('qualify', (entity) => {
      //- console.log(`PDFViewerSystem: entity ${entity.id} qualified`);
    });

    this.queries.loadPDFRequests.subscribe('qualify', (entity) => {
      //- console.log(`PDFViewerSystem: entity ${entity.id} qualified`);
    });
  }

  initStatusProperties() {
    
    if (this.pdfDoc) {
      this.pdfDoc.cleanup();
    }

    this.pdfDoc = null;
    this.pdfDocMetadata = null;
    this.pageNum = 0;
    this.bookInfo = null;
    // Calculate the ideal scale for high resolution
    this.outputScale = DESIRED_DPI / 72; // PDFs use 72 DPI as standard
  }

  update(_delta) {
    // const readerRequests = this.getEntities(this.queries.readerRequests);
    const loadBookRequests = Array.from(this.queries.loadPDFRequests.entities);
    const pageRequests = Array.from(this.queries.pageRequests.entities);

    const renderingInProgress = pageRequests.some(requestEntity => {
      const status = requestEntity.getValue(PDFRequestComponent, 'status');
      return status === "rendering";
    });

    const loadingInProgress = loadBookRequests.some(requestEntity => {
      const status = requestEntity.getValue(LoadPDFRequestComponent, 'status');
      return status === "loading";
    });

    if(renderingInProgress || loadingInProgress) {
      return;
    }

    this.processLoadPDFRequests(loadBookRequests);
    this.processPageRequests(pageRequests);
  }

  processLoadPDFRequests(pdfRequests) {
    if (pdfRequests.length === 0) return;

    const requestEntity = pdfRequests[0];
    const payload = requestEntity.getValue(LoadPDFRequestComponent, 'payload');
    
    switch(payload.type) {
      case "loadPdf":
        this.loadPdf(payload.pdfUrl, payload.source, requestEntity);
        break;
      default:
        globals.debug(`Unknown request type: ${payload.type}`);
        break;
    }
  }

  processPageRequests(pageRequests) {
    if (pageRequests.length === 0) return;
    
    const requestEntity = pageRequests[0];
    const payload = requestEntity.getValue(PDFRequestComponent, 'payload');

    switch(payload.type) {
      case "loadPage":
        this.renderPage(payload.pageNum, requestEntity);
        break;
      default:
        globals.debug(`Unknown request type: ${payload.type}`);
        break;
    }
  }

  async loadPdf(url, source, requestEntity) {

    // every update cycle this will be called, until the viewer is ready
    if(!viewer.status.ready) {
      return;
    }

    try {
      if (!url) {
        // FIXME won't work in XR
        // alert("Please enter a valid PDF URL.");
        globals.status("Please enter a valid PDF URL.");
        return;
      }

      requestEntity.setValue(LoadPDFRequestComponent, 'status', "loading");

      this.initStatusProperties();

      globals.status("Loading new PDF...");

      // this.pdfDoc = await pdfjsLib.getDocument({
      //     url: url,
      //     disableAutoFetch: true, // Reduces worker usage
      //     disableStream: true    // Further reduces worker involvement
      // }).promise;

      if (source === "local") {
        // this.pdfDoc = await pdfjsLib.getDocument(url).promise;
        await viewer.open_document_from_file(url);
      } else {
        await viewer.open_document_from_url(url);
      }

      console.log("Document mupdf loaded", viewer.document_info);

      globals.status("PDF loaded");

      URL.revokeObjectURL(url);

      // this.pdfDocMetadata = await this.pdfDoc.getMetadata();
      // this.pageNum = 1;

      this.bookInfo = {
        numPages: viewer.document_info.num_pages,
        // ...this.pdfDocMetadata.info,
        ...viewer.document_info.metadata,
      }

      const payload = {
        bookInfo: this.bookInfo,
        newBook: true,
      };

      // // Notify the cache system to update the page shown in the book
      this.world.createEntity().addComponent(CacheUpdateBookComponent, { payload });

      requestEntity.removeComponent(LoadPDFRequestComponent).destroy();
    } catch(error) {
      globals.status("ERROR loading PDF, check debug console for details");
      globals.debug(`Error loading PDF: ${error}`);
      console.error("Error loading PDF:", error);
    }
  }

  destroyRequest(requestEntity) {
    if(!requestEntity) return;

    requestEntity.removeComponent(PDFRequestComponent).destroy();
  }

  updateRequestStatus(requestEntity, status) {
    if(!requestEntity) return;

    requestEntity.setValue(PDFRequestComponent, 'status', status);
  }

  /**
   * @param num Page number.
   */
  async renderPage(num, requestEntity) {

    console.log(`renderPage: ${num} page requested`);

    if (num <=0 || num > this.bookInfo.numPages) {
      // The request makes no sense, ignore it and destroy 
      // the request to avoid processing it again and again
      this.destroyRequest(requestEntity);
      
      return;
    }

    this.updateRequestStatus(requestEntity, 'rendering');

    // console.log(`renderPage: ${num}, ${newPdf} page requested`);


    // const page = await this.pdfDoc.getPage(num);

    // globals.debug(`renderPage: ${num} page loaded`);


    /////////////////////////////////////////////////////////////////////////////////
    // WARNING: for some (unknown) reason, some of the rendering params given here //
    // are required for the page to show correctly on the Quest XR                 //
    /////////////////////////////////////////////////////////////////////////////////
    // const viewport = page.getViewport({
    //   scale: this.outputScale,
    //   offsetX: 0,
    //   offsetY: 0,
    //   dontFlip: false,
    // });

    // globals.gl.clearRect(0, 0, globals.offscreen.width, globals.offscreen.height);
    
    // globals.offscreen.width = viewport.width;
    // globals.offscreen.height = viewport.height;

    // Render PDF page into offscreen canvas context
    // const renderContext = {
    //   canvasContext: globals.gl,
    //   viewport: viewport,
    //   intent: 'print',
    //   imageLayer: false,
    //   enableWebGL: true,
    //   renderInteractiveForms: false,
    // };

    // FIXME: after this line, for some (unknown) combination of parameters,
    // page image rendering on Quest XR starts failing: sometimes the image is not rendered, 
    // others the old image is replaced by blank one, others Browser crashes...
    // await page.render(renderContext).promise;

    // globals.debug(`renderPage: ${num} page rendered`);

    // this.pageRendering = false;

    // page.cleanup();

      // globals.debug(`renderPage: ${num} rendering completed`);
      
    const imageData = await viewer.show_page(num);
    
    const payload = {
      pageNum: num,
      // pageWidth: globals.offscreen.width,
      // pageHeight: globals.offscreen.height,
      pageWidth: imageData.width,
      pageHeight: imageData.height,
      imageData: imageData,
      bookInfo: this.bookInfo,
      newBook: false,
    };

      this.pageNum = num;

      // Notify the ReaderSystem to update the page shown in the book
      this.world.createEntity().addComponent(CacheUpdatePageComponent, { payload });

      this.destroyRequest(requestEntity);

      //- console.log(`renderPage: CacheUpdatePageComponent`, payload);
  }

}


