import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import {
  Group,
  PerspectiveCamera,
  Scene,
  TextureLoader,
  WebGLRenderer,
  XRGripSpace,
  XRTargetRaySpace,
} from "three";
// import { PDFViewer } from "./pdfviewer";
import { RealityAccelerator } from "ratk";

/**
 * @type {{ renderer: WebGLRenderer,
 * 			camera: PerspectiveCamera,
 * 			scene: Scene,
 * 			ratk: RealityAccelerator,
 * 			playerHead: Group,
 * 			controllers: {
 * 				left: {raySpace: XRTargetRaySpace, gripSpace: XRGripSpace, gamepad: GamepadWrapper, 
 * 						userData: {controlsPanel: Mesh, pointer: Mesh, purgeRing: Mesh}
 * 				},
 * 				right: {raySpace: XRTargetRaySpace, gripSpace: XRGripSpace, gamepad: GamepadWrapper,
 * 						userData: {controlsPanel: Mesh}
 * 				},
 * 			textureLoader: TextureLoader,
 * 			gltfLoader: GLTFLoader,
 * 			valueStore: Map,
 * 			pdfViewer: PDFViewer,
 * }}
 */
export const globals = {
  renderer: undefined,
  camera: undefined,
  scene: undefined,
  ratk: undefined,
  playerHead: undefined,
  controllers: undefined,
  textureLoader: new TextureLoader(),
  gltfLoader: new GLTFLoader(),
  valueStore: new Map(),
  // pdfViewer: undefined,
  imageData: null,
  offscreen: null,
  gl: null,
  status: (msg) => {
    document.getElementById("status-msg").innerText = `Status: ${msg}`;
  },
  debug: (...msg) => {
    const debugMessages = document.getElementById("debug-messages");
    if (!debugMessages) {
      return;
    }

    const ts = new Date().toLocaleString('sv-SE', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false
    });

    msg.forEach((m) => {
      if (typeof m === "object") {
        m = JSON.stringify(m);
      }
      const tsmsg = `[${ts}] ${m}`;
      const prevVal = debugMessages.value;
  
      debugMessages.value += `${prevVal}${tsmsg}\n`;
    });
  },
};
